import React from 'react';
import styles from './Subscribe.module.scss';
import arrow from "../../assets/arrow.svg";
import checkmark from "../../assets/checkmark.svg";
import logo from "../../assets/sun-logo.svg";

const Subscribe = () => {
    return (
        <div className={styles.subscribe}>
            <div className={styles.subscribe__logo}><img src={logo} alt="logo"/>SunnyTour</div>
            <div className={styles.subscribe__wave}>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span className={styles.space}></span>
                <span></span>
                <span></span>
                <span className={styles.space}></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span className={styles.space}></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
            </div>
            <h6 className={styles.subscribe__subtitle}>  SunnyTour
                   </h6>
            <div className={styles.subscribe__mail}>
                <input
                    className={styles.subscribe__input}
                    type="email"
                    placeholder=" E-mail"
                />
                <img className={styles.subscribe__arrow} src={arrow} alt={arrow}/>
            </div>
            <div className={styles.subscribe__sub}>
                <img className={styles.subscribe__sub_checkmark} src={checkmark} alt={checkmark}/>
                <h5 className={styles.subscribe__sub_text}> !</h5>
            </div>
        </div>
    );
};

export default Subscribe;