import React from 'react';
import styles from './Overview.module.scss';

const Overview = () => {
    return (
        <div className={styles.overview}>
            <h1 className={styles.overview__title}><span>SunnyTour</span> 
                 <span></span>,<br/>       .<br/> 
                 <span>  7 </span>       .
            </h1>
            <div className={styles.overview__tags}>
                <span className={`${styles.overview__tags_tag} ${styles.tag1}`}></span>
                <span
                    className={`${styles.overview__tags_tag} ${styles.overview__tags_tagEffect} ${styles.tag2}`}></span>
                <span className={`${styles.overview__tags_tag} ${styles.tag3}`}></span>
                <span className={`${styles.overview__tags_tag} ${styles.tag4}`}></span>
                <span
                    className={`${styles.overview__tags_tag} ${styles.overview__tags_tagEffect} ${styles.tag5}`}></span>
                <span
                    className={`${styles.overview__tags_tag} ${styles.overview__tags_tagEffect} ${styles.tag6}`}></span>
                <span className={`${styles.overview__tags_tag} ${styles.tag7}`}></span>
                <span className={`${styles.overview__tags_tag} ${styles.tag8}`}></span>
                <span className={`${styles.overview__tags_tag} ${styles.tag9}`}></span>
                <span className={`${styles.overview__tags_tag} ${styles.tag10}`}></span>
                <span className={`${styles.overview__tags_tag} ${styles.tag11}`}> </span>
                <span
                    className={`${styles.overview__tags_tag} ${styles.overview__tags_tagEffect} ${styles.tag12}`}></span>
            </div>
        </div>
    );
};

export default Overview;